.newsletter {
    .left {
        
    }

    .right {
        opacity: 0.2;
        background: black;
    }
}

main {
    padding: 1rem 0;
    
    display: flex;
    grid-gap: 1rem;
    flex-direction: column;
}

.ui.grid.x { margin: 0; }


section {
    width: 100%;
    margin: 0rem 0;
    display: inline-block;
}

.ui.container {
    background-color: white;
}

#content {
    min-height: 100px;
}
.ui.grid{
    padding: 0 !important;
}
.pushable.segment{
    margin: 0 !important;
}



.splide {
    visibility: visible;
    
    .splide__slide {
        color: black;
        
        img {
            width: 100%;
            height: 100%;
        }
        
        p {
            max-height: 125px;
            padding: 0.5rem;
            padding-bottom: 20px;
            
            color: rgba(0, 0, 0, .8);
            letter-spacing: 2px;
            
            bottom: 0;
            position: absolute;
            background: rgba(white, .75);
        }
        
        &:hover,
        &:active {
            color: black;
        }
    }
    
}

.profil {
    display: flex;
    flex-flow: row wrap;
    
    position: relative;
    
    .picture {
        overflow: hidden;
        border-radius: 10px;
    }
    
    .details {
        flex: 1;
        padding: 1rem;
        
    }
    
    &.over {
        width: 200px;
        
        overflow: hidden;
        border: 2px solid gray;
        border-radius: 10px;       
        
        &:hover {
            .details { display: flex; }
        }
        
        .details {
            width: 100%;
            height: 100%;
            
            display: none;
            flex-direction: column;
            justify-content: center;
            
            top: 0;
            left: 0;
            position: absolute;
            
            opacity: 0.7;
            background: white;
            
            text-align: center;
        }   
    }
}

